%hover-opacity {
  transition: opacity var(--base-transition);

  @media (hover: hover) {
    &:hover {
      opacity: 0.7;
    }
  }

  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.4;
  }
}

%base-hover {
  transition: color var(--base-transition);

  @media (hover: hover) {
    &:hover {
      color: var(--primary);
    }
  }

  &:focus {
    color: var(--primary);
  }

  &:active {
    color: var(--primary);
    opacity: 0.7;
  }
}

%img-blur {
  background-color: rgb(245, 245, 245, 50%);
  & > img {
    filter: blur(30px);
  }
}
